import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import { useState } from 'react';
import './Input.scss';

function Input({ type, placeholder, id, name }) {
  const [hasValue, setHasValue] = useState(false);

  const handleClick = () => {
    const element = document.getElementById(id);
    element.type = element.type == 'password' ? 'text' : 'password';

  }

  

  return (

    <div className='input'>
      <input type={type} name={name }placeholder={placeholder} id={id}onInput={(e) => setHasValue( e.target.value.length > 0)}/>
      {type == 'password' && hasValue ? (
      <VisibilityOffIcon onClick={(e) => handleClick() }/>
      ) : ""}

    </div>
  )
}

export default Input;
