import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import './Login.scss';
import GoogleIcon from '@mui/icons-material/Google';
import Input from './input/Input.jsx';
import Button from '@mui/material/Button';
import { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

function Login({ togglePanel }) {
    const [toggleEye, setToggleEye] = useState(true);
    const [user, setUser] = useState({});

    const handleAction = (e) => {
        e.preventDefault();
        let email = Array.from(e.target)[0].value;
        let password = Array.from(e.target)[1].value;

        const fetchUser = async () => {
            let data = await fetch(`http://localhost:5000/login`, {
                method: 'POST',
                body: JSON.stringify({ email, password }),
                headers: { "Content-Type": "Application/json" }
            });

            const result = await data.json();
            
            


        } 

        fetchUser();

    }
    return (

        <div className="login-right-container">
            <p onClick={() => togglePanel()}> Not a member? <span>Register now</span></p>

            <div className="top">


                <h1> Welcome! </h1>
            </div>

            <div className="bottom">

                <form action='/api/login' method='POST'>

                    <Input type="text" name="email" id="input-text" placeholder="Enter username"/>
                    <Input type="password" name="password" id="input-pass" placeholder="Password"/>

{/*                     <input type="text" placeholder='Enter username' />
                    <input type={toggleEye ? "password" : "text"} placeholder='Password' />
                    <VisibilityOffIcon className='eye-2' onMouseDown={() => setToggleEye(!toggleEye)} onMouseUp={() => setToggleEye(!toggleEye)} /> */}


                    <span> <a href=""> I forgot my password </a></span>

                    {/*<button>Sign in</button>*/}

                    <Button  variant='contained'> Sign In </Button>

                </form>

                <span>Or continue with</span>

                <div className="socials">

                    <div className="icon-google">
                        <img src="images/google.png" alt="" />
                    </div>
                    <div className="icon-facebook">
                        <img src="images/facebook.png" alt="" />
                    </div>
                    <div className="icon-twitter">
                        <img src="images/twitter.png" alt="" />
                    </div>



                </div>


            </div>

        </div>

    )
}

export default Login;
