import '../../global.scss';
import './Signup.scss';
import Input from './Input/Input.jsx';
import Button from '@mui/material/Button';
import { Link } from 'react-router-dom';


function Signup({ togglePanel }) {

    const handleSubmission = async (e) => {
        e.preventDefault();
        const form = e.target;
        const userRegex = /^[A-Za-z][A-Za-z0-9_]{7,29}$/;
        const nameRegex = /^[A-Za-z][A-Za-z]{3,15}$/;
        
        const firstName = form.elements['first-name'].value;
        const lastName = form.elements['last-name'].value;
        const email = form.elements.email.value;
        const password = form.elements.password.value;
        const confirmedPassword = form.elements['confirmed-password'].value;

        if (password !== confirmedPassword || !firstName || !lastName || !email || !password || !confirmedPassword ) {
            return console.log('Please fill up all the fields')
        } else if (!nameRegex.test(firstName)) {
            return console.log("Please enter a valid name");
            
        } else {
            const createUser = await fetch('http://192.119.90.44:5000/api/register', {
                method: 'POST',
                body: JSON.stringify({ firstName, lastName, email, password}),
                headers: { "Content-Type": "application/json"}
            });

            form.reset();
            window.location.assign('http://192.119.90.44:5000/')

        }
        


        

        
        





        

    }
    return (
        <div className='signup-right-container'>
                            <h1 onClick={() => togglePanel()}>SIGN UP or <span>LOGIN</span>  </h1>

            <form action="/api/register" method="post" onSubmit={(e) => handleSubmission(e)}>


                <div className="top">



                    <input type="text" name="first-name" placeholder='First name' />
                    <input type="text" name="last-name" placeholder='Last name' />

                </div>

                <div className="center">
                    <Input type="email" name="email" placeholder='Email address' />
                    <Input type="password" name="password" placeholder='Password' id={'pass-01'}/>
                    <Input type="password" name="confirmed-password" placeholder='Confirm password' id={'pass-02'}/>
                </div>

                <div className="bottom">
                    <span> Date of birth </span>
                    <input type="date" name="date" id="" />
                    <Button type='submit' variant='contained'> Submit </Button>

                </div>





            </form>


        </div>
    )
}

export default Signup
